@use '../../../Public/SCSS/Utilities/Variables' as *;

/////////////////////////////////
// Base

html {
  font-family: Purely Me, Century Gothic, Verdana, Sans-Serif;
  font-variant-ligatures: none;
}

/////////////////////////////////
// Master Grid Container
.master-grid-container {
  .application-flex-container {
    .application-viewport {
      .application-display {
        .budget-creation-form-container {
          .form-close-icon,
          .budget-creation-form-close-icon {
            font-size: 4rem;
            color: rgba($primaryColor, 0.5);
            &:hover {
              color: $primaryColor;
            }
          }
          .budget-creation-form {
            font-family: MADE Tommy Soft, Verdana, sans-serif;
            &__header {
              &__title {
                font-family: MADE Tommy Soft Bold, Verdana, sans-serif;
                font-size: 10rem;
                color: $primaryColor;
                &--medium {
                  font-family: MADE Tommy Soft Bold, Verdana, sans-serif;
                  color: $primaryColor;
                  font-size: 6rem;
                }
              }
            }
            &__page {
              &:nth-of-type(1),
              &:nth-of-type(5),
              &:nth-of-type(6),
              &:nth-of-type(7) {
                flex-direction: column;
                & > .budget-creation-form__page__section {
                  & > .budget-creation-form__page__section__input {
                    font-size: 1.6rem;
                  }
                  & > .budget-creation-form__page__section__label {
                    font-size: 1.6rem;
                  }
                }
              }
              &:nth-of-type(8) {
                flex-direction: column;
                & > .budget-creation-form__page__section {
                  & > .budget-creation-form__page__section__input {
                    font-size: 1.6rem;
                  }
                  & > .budget-creation-form__page__section__label {
                    font-size: 1.6rem;
                  }
                }
              }
              &__section {
                &__select {
                  color: rgba($colorOffWhite, 0.8);
                  font-size: 2rem;
                  text-align: center;
                  &:focus {
                    background-color: $primaryColor;
                    border: 0.3rem solid $primaryColor;
                    color: $colorOffWhite;
                  }
                  option,
                  &__option {
                    font-family: MADE Tommy Soft, Verdana, sans-serif;
                    color: $colorOffWhite;
                    font-weight: 300;
                  }
                }
                &__input {
                  font-size: 2rem;
                  color: rgba($primaryColor, 0.8);
                  &:focus {
                    color: $primaryColor;
                    & > label {
                      color: $primaryColor;
                    }
                  }
                }
                &__label {
                  font-size: 2rem;
                  color: rgba($primaryColor, 0.8);
                }
                &__button {
                  font-size: 2.2rem;
                  color: rgba($primaryColor, 0.75);
                  &:hover {
                    color: $colorOffWhite;
                  }
                }
                &__main-category-container {
                  &__create-main-category {
                    font-size: 2rem;
                    color: rgba($primaryColor, 0.8);
                    &__icon {
                      font-size: 3rem;
                    }
                    &:hover {
                      .budget-creation-form__page__section__main-category-container__create-main-category__icon {
                        color: $colorOffWhite;
                      }
                      .budget-creation-form__page__section__main-category-container__create-main-category__text {
                        color: $colorOffWhite;
                      }
                    }
                    &__icons-container {
                      .icon-container {
                        font-family: MADE Tommy Soft, Verdana, sans-serif !important;
                        font-size: 1.6rem;
                        color: $primaryColor;
                        text-align: center;
                        &__icon {
                          font-size: 4rem;
                        }
                        &__text {
                        }
                        &:hover {
                          color: $colorOffWhite;
                        }
                        &--clicked {
                          color: $colorOffWhite;
                        }
                      }
                    }
                  }
                  .main-category {
                    font-family: MADE Tommy Soft, Verdana, sans-serif;
                    font-size: 2rem;
                    color: $primaryColor;
                    &__icon {
                      font-size: 3rem;
                    }
                    &__delete {
                      font-size: 1.2rem;
                      color: rgba($primaryColor, 0.8);
                    }
                    &:hover {
                      color: $colorOffWhite;
                      .main-category__delete {
                        color: rgba($colorOffWhite, 0.8);
                        &:hover {
                          color: $primaryColor;
                          // transition: color 0.5s;
                        }
                      }
                    }
                  }
                }
                &__set-main-category-title-container {
                  &__input {
                    font-family: MADE Tommy Soft, Verdana, sans-serif;
                    font-size: 1.6rem;
                    color: rgba($primaryColor, 0.8);
                    &:focus {
                      color: $primaryColor;
                    }
                  }
                  &__button {
                    font-family: MADE Tommy Soft, Verdana, sans-serif;
                    font-size: 1.6rem;
                    color: rgba($primaryColor, 0.8);
                    &:hover {
                      color: $colorOffWhite;
                    }
                  }
                }
                &__sub-category-container {
                  &__main-category-display {
                    &__left-button,
                    &__right-button {
                      &__icon {
                        font-size: 5rem;
                        color: rgba($primaryColor, 0.8);
                        &:hover {
                          color: $primaryColor;
                        }
                      }
                    }
                    &__left-button {
                      &__icon {
                        color: rgba($primaryColor, 0.25);
                      }
                    }
                    &__category-information {
                      color: $primaryColor;
                      &__icon {
                        font-size: 16rem;
                      }
                      &__text {
                        font-size: 4rem;
                      }
                    }
                  }
                  &__sub-category-display {
                    &__sub-category {
                      .sub-category-title-container {
                        &__title {
                          font-family: MADE Tommy Soft, Verdana, sans-serif;
                          font-size: 1.6rem;
                          color: $primaryColor;
                        }
                      }
                      .sub-category-controller {
                        font-family: MADE Tommy Soft, Verdana, sans-serif;
                        font-size: 1.6rem;
                        color: $primaryColor;
                        &__surplus-container {
                          &__switch {
                            &__toggle {
                              &__icon {
                                font-size: 1.2rem;
                                color: rgba($colorOffWhite, 0.8);
                              }
                            }
                          }
                        }
                        &__icon {
                          color: rgba($primaryColor, 0.8);
                          font-size: 2rem;
                          &:hover {
                            color: $primaryColor;
                          }
                        }
                      }
                    }
                    .category-creation {
                      &__input-container {
                        font-size: 1.4rem;
                        &__input {
                          color: rgba($primaryColor, 0.8);
                          &:focus {
                            color: $primaryColor;
                          }
                        }
                        &__button {
                          color: rgba($primaryColor, 0.8);
                          &:hover {
                            color: $colorOffWhite;
                          }
                        }
                      }
                      &__controller {
                        font-family: MADE Tommy Soft, Verdana, sans-serif;
                        font-size: 1.6rem;
                        color: $primaryColor;
                        &__surplus-container {
                          &__switch {
                            &__toggle {
                              &__icon {
                                font-size: 1.2rem;
                                color: rgba($colorOffWhite, 0.8);
                              }
                            }
                            &--switched {
                              .category-creation-controller__surplus-container__switch__toggle {
                              }
                            }
                            &:hover {
                              .category-creation-controller__surplus-container__switch__toggle {
                              }
                            }
                          }
                        }
                        &__icon {
                          color: rgba($primaryColor, 0.8);
                          font-size: 2rem;
                          &:hover {
                            color: $primaryColor;
                          }
                        }
                        &__close {
                          font-size: 1.4rem;
                          color: rgba($primaryColor, 0.8);
                          &:hover {
                            color: $colorOffWhite;
                          }
                        }
                      }
                    }
                    &__sub-category-button {
                      font-family: MADE Tommy Soft, Verdana, sans-serif;
                      font-size: 1.6rem;
                      color: rgba($primaryColor, 0.8);
                      &:hover {
                        color: $primaryColor;
                      }
                    }
                  }
                }
                &__month-container {
                  &__header {
                    font-size: 2.8rem;
                    color: $colorOffWhite;
                    &__title {
                      font-size: 4rem;
                      text-transform: uppercase;
                    }
                  }
                  &__budget-goal-summary-container {
                    .budget-single-goal-summary {
                      &__title {
                        font-size: 1.6rem;
                        color: $primaryColor;
                        text-align: center;
                      }
                      &__amount {
                        font-size: 3rem;
                        color: $primaryColor;
                      }
                    }
                  }
                  &__category-container {
                    .main-category-display {
                      &__button-container {
                        font-size: 6rem;
                        .left {
                          color: rgba($primaryColor, 0.25);
                        }
                        .right {
                          color: rgba($primaryColor, 0.8);
                        }
                        .left,
                        .right {
                          &:hover {
                            color: $primaryColor;
                          }
                        }
                      }
                      &__category-display {
                        color: $primaryColor;
                        &__icon {
                          font-size: 20rem;
                        }
                        &__title {
                          font-size: 4rem;
                        }
                      }
                    }
                    .sub-category-display {
                      &__headers {
                        &__title-container {
                          font-size: 1.2rem;
                          color: $colorOffWhite;
                          text-align: center;
                        }
                      }
                      &__sub-category {
                        &__section {
                          &__category-name {
                            font-size: 1.6rem;
                            color: $primaryColor;
                          }
                          &:first-of-type {
                            text-align: center;
                          }
                          &__set-category-timing-button {
                            font-size: 1.2rem;
                            color: rgba($primaryColor, 0.8);
                          }
                          &__input {
                            font-size: 2rem;
                            color: rgba($primaryColor, 0.8);
                            text-align: center;
                            &:focus {
                              color: $primaryColor;
                            }
                          }
                          &__content {
                            font-size: 2rem;
                            color: rgba($primaryColor, 0.8);
                            text-align: center;
                          }
                        }
                        &:hover {
                          .sub-category-display__sub-category__section {
                            background-color: $primaryColor;
                            &__category-name {
                              color: $colorOffWhite;
                            }
                            &__set-category-timing-button {
                              color: rgba($colorOffWhite, 0.8);
                              &:hover {
                                color: $colorOffWhite;
                              }
                            }
                            &__input {
                              color: rgba($colorOffWhite, 0.8);
                              &:focus {
                                color: $primaryColor;
                              }
                            }
                            &__content {
                              color: $colorOffWhite;
                            }
                          }
                        }
                      }
                      &__timing-container {
                        &__close {
                          font-size: 3rem;
                          color: rgba($primaryColor, 0.8);
                          &:hover {
                            color: $primaryColor;
                          }
                        }
                        &__monthly-container,
                        &__bi-monthly-container,
                        &__bi-weekly-container,
                        &__weekly-container {
                          &__button {
                            &__icon {
                              font-family: Icomoon;
                              font-size: 4rem;
                              .path1:before {
                                color: $primaryColor;
                              }
                            }
                            &__text {
                              font-size: 2.2rem;
                              color: rgba($primaryColor, 0.8);
                            }
                            &:hover {
                              .sub-category-display__timing-container {
                                &__monthly-container,
                                &__bi-monthly-container,
                                &__bi-weekly-container,
                                &__weekly-container {
                                  &__button {
                                    &__icon {
                                      .path1:before {
                                        color: $tertiaryColor;
                                      }
                                      .path2:before,
                                      .path3:before {
                                        color: $colorDarkerGrey;
                                      }
                                    }
                                    &__text {
                                      color: $colorOffWhite;
                                    }
                                  }
                                }
                              }
                            }
                          }
                          &__label {
                            font-size: 1.4rem;
                            color: rgba($primaryColor, 0.8);
                            &:focus {
                              color: $primaryColor;
                            }
                            &__input,
                            &__select {
                              font-size: 1.4rem;
                              color: rgba($primaryColor, 0.8);
                              &:focus {
                                color: $primaryColor;
                              }
                            }
                          }
                          &__submit {
                            font-size: 2rem;
                            color: rgba($colorOffWhite, 0.8);
                            &:hover {
                              color: $colorOffWhite;
                            }
                          }
                        }
                      }
                    }
                  }
                }
              }
              .emergency-section {
                &-header {
                  font-weight: 300;
                  color: $primaryColor;
                }
                #lengthOfTime,
                #totalAmount {
                  & + .clicked-label {
                    color: $colorOffWhite;
                  }
                }
              }
              &-mechanisms {
                &__page-number {
                  font-size: 3rem;
                  color: $primaryColor;
                }
                &__submit-button {
                  font-size: 2rem;
                  color: rgba($primaryColor, 0.8);
                  &:hover {
                    color: $colorOffWhite;
                  }
                }
              }
              .tithing-section {
                #grossOption,
                #netOption,
                #surplusOption {
                }
                #grossOptionLabel,
                #netOptionLabel,
                #surplusOptionLabel {
                  &:hover {
                    color: $colorOffWhite;
                  }
                }
                .checked {
                  color: $colorOffWhite;
                }
              }
            }
          }
        }
        .budget-container {
          .budget-navigation {
            &__link-list {
              &__list-item {
                &__link,
                &__link-button {
                  font-family: MADE Tommy Soft, Verdana, sans-serif;
                  font-size: 2.4rem;
                  text-decoration: none;
                  color: rgba($colorOffWhite, 0.8);
                  &:hover {
                    color: $colorOffWhite;
                  }
                  &:active {
                    color: $colorDarkerGrey;
                  }
                }
                &__sub-link-container {
                  font-family: MADE Tommy Soft, Verdana, sans-serif;
                  font-size: 2rem;
                  &__sub-link {
                    color: rgba($colorOffWhite, 0.8);
                    text-decoration: none;
                  }
                  &:hover {
                    .budget-navigation__link-lidt__link-item__sub-link-container__sub-link {
                      color: $colorOffWhite;
                    }
                  }
                  &:active {
                    .budget-navigation__link-lidt__link-item__sub-link-container__sub-link {
                      color: $colorDarkerGrey;
                    }
                  }
                }
              }
            }
          }
          &__navigation-button-container {
            &__text {
              font-family: MADE Tommy Soft, Verdana, sans-serif;
              font-size: 2.8rem;
              color: $primaryColor;
            }
            &__button {
              &--clicked {
                & ~ .budget-container__navigation-button-container__text {
                  color: rgba($primaryColor, 0.2);
                }
              }
            }
          }
          &__dashboard {
            font-family: MADE Tommy Soft, Verdana, sans-serif;
            color: $colorOffWhite;
            &__container {
              &--extra-small {
                &__header {
                  font-size: 2.4rem;
                  &__icon {
                    font-size: 3rem;
                  }
                  &__text {
                  }
                }
                &__content {
                  font-size: 4rem;
                  color: $primaryColor;
                }
              }
              &--small {
                &__header {
                  font-size: 2.4rem;
                  &__icon {
                    font-size: 3rem;
                  }
                  &__text {
                  }
                  &__button {
                    color: rgba($colorOffWhite, 0.8);
                    &:hover {
                      color: $colorOffWhite;
                    }
                    &:active {
                      color: $colorDarkerGrey;
                    }
                  }
                }
                &__content {
                  color: $primaryColor;
                  .enter-income-form,
                  .enter-transaction-form {
                    .form-row {
                      .account-selection {
                        color: rgba($primaryColor, 0.8);
                        font-size: 1.4rem;
                        &:hover {
                          color: $colorOffWhite;
                        }
                        &:focus {
                          border: 0.2rem solid $primaryColor;
                        }
                      }
                      &__section {
                        .form-input {
                          color: rgba($primaryColor, 0.8);
                          font-size: 1.4rem;
                          &:focus {
                            color: $primaryColor;
                          }
                        }
                        .form-label {
                          font-size: 1.4rem;
                          .form-input--tithing {
                            color: $colorOffWhite;
                          }
                        }
                      }
                      .form-label {
                        .form-input {
                          color: rgba($primaryColor, 0.8);
                          font-size: 1.4rem;
                          &:hover {
                            color: $colorOffWhite;
                          }
                        }
                      }
                    }
                  }
                  .enter-transaction-form {
                    .form-row {
                      &:nth-of-type(1) {
                        .form-button {
                          color: rgba($primaryColor, 0.8);
                          font-size: 1.4rem;
                          &:hover {
                            color: $colorOffWhite;
                          }
                          &:focus {
                            border: 0.2rem solid $primaryColor;
                          }
                        }
                      }
                      &:nth-of-type(3) {
                        .label-container {
                          font-size: 1.26rem;
                          text-align: left;
                          select {
                            font-size: 1.4rem;
                          }
                        }
                      }
                      &:nth-of-type(4) {
                        .fully-paid-for {
                          .paid-for-container {
                            &__no,
                            &__yes {
                              color: $colorOffWhite;
                            }
                          }
                        }
                      }
                      &:nth-of-type(3),
                      &:nth-of-type(4) {
                        .form-row__section {
                          &:nth-of-type(0),
                          &:nth-of-type(1),
                          &:nth-of-type(2),
                          &:nth-of-type(3),
                          &:nth-of-type(4),
                          &:nth-of-type(6),
                          &:nth-of-type(7),
                          &:nth-of-type(9),
                          &:nth-of-type(10),
                          &:nth-of-type(12),
                          &:nth-of-type(13) {
                            width: 50%;
                            .form-input {
                              width: 90%;
                            }
                          }
                        }
                      }
                    }
                  }
                  .early-income-view {
                    &__investment,
                    &__savings,
                    &__un-allocated {
                      font-size: 3.2rem;
                      color: $primaryColor;
                      &__header {
                        font-size: 1.2rem;
                      }
                    }
                    &__un-allocated {
                      color: $colorOffWhite;
                    }
                  }
                }
                &__transaction-total {
                  font-family: MADE Tommy Soft, Verdana, sans-serif;
                  font-weight: bold;
                  font-size: 6rem;
                  &__heading {
                    font-family: MADE Tommy Soft, Verdana, sans-serif;
                    font-weight: normal;
                    font-size: 2rem;
                  }
                }
                &-wide {
                  .bill-calendar-container {
                    &__header {
                      font-size: 2.4rem;
                      &__icon {
                        font-size: 3rem;
                      }
                    }
                    &__calendar-container {
                      &__calendar {
                        &__header {
                          font-size: 2.4rem;
                          color: $primaryColor;
                          &__icon {
                            font-size: 3rem;
                            color: rgba($primaryColor, 0.25);
                            &--not-end {
                              &:hover {
                                color: $primaryColor;
                              }
                            }
                            &:hover {
                              color: $primaryColor;
                            }
                          }
                        }
                        &__weekdays {
                          &__weekday {
                            font-size: 1.6rem;
                          }
                        }
                        &__days {
                          &__single-day {
                            font-size: 2rem;
                            color: rgba($primaryColor, 0.8);
                            &:hover {
                              color: $colorOffWhite;
                            }
                            &--current-day {
                              color: $colorOffWhite;
                            }
                          }
                          .un-used-day {
                            &:hover {
                              background-color: $primaryColor;
                            }
                          }
                        }
                      }
                    }
                    &__upcoming-bills {
                      &__titles {
                        font-family: MADE Tommy Soft, Verdana, sans-serif;
                        color: $colorOffWhite;
                        &__title {
                          font-size: 1.6rem;
                        }
                      }
                      &__bills {
                        font-family: MADE Tommy Soft, Verdana, sans-serif;
                        &__bill {
                          font-size: 1.6rem;
                          color: rgba($primaryColor, 0.8);
                          &__bill-item {
                            text-align: center;
                            &:nth-of-type(5) {
                              input[type='checkbox'] {
                                font-size: 1.2rem;
                                &::after {
                                  font-family: 'Font Awesome 5 Free';
                                  font-weight: 900;
                                }
                                &:checked {
                                  &:after {
                                    color: $colorOffWhite;
                                  }
                                }
                              }
                            }
                          }
                          &:hover {
                            color: $colorOffWhite;
                          }
                        }
                      }
                    }
                    &__calendar-container,
                    &__upcoming-bills {
                    }
                  }
                }
              }
              &--large {
                font-family: MADE Tommy Soft, Verdana, sans-serif;
                &__header {
                  &__title {
                    font-size: 4.5rem;
                  }
                  &__icon {
                    font-size: 3rem;
                  }
                }
                &__budget-goal-summary-container {
                  .budget-single-goal-summary {
                    &__title {
                      font-size: 1.6rem;
                      color: $primaryColor;
                      text-align: center;
                    }
                    &__amount {
                      font-size: 3rem;
                      color: $primaryColor;
                    }
                  }
                }
                &__category-container {
                  .main-category-display {
                    &__button-container {
                      font-size: 6rem;
                      .left {
                        color: rgba($primaryColor, 0.25);
                      }
                      .right {
                        color: rgba($primaryColor, 0.8);
                      }
                      .left,
                      .right {
                        &:hover {
                          color: $primaryColor;
                        }
                      }
                    }
                    &__category-display {
                      color: $primaryColor;
                      &__icon {
                        font-size: 20rem;
                      }
                      &__title {
                        font-size: 4rem;
                      }
                    }
                  }
                  .sub-category-display {
                    &__headers {
                      &__title-container {
                        font-size: 1.6rem;
                        color: $colorOffWhite;
                        text-align: center;
                      }
                    }
                    &__sub-category {
                      &__section {
                        &__content {
                          font-size: 1.6rem;
                          color: $primaryColor;
                        }
                      }
                      &:hover {
                        .sub-category-display__sub-category__section {
                          &__content {
                            color: $colorOffWhite;
                          }
                        }
                      }
                    }
                  }
                }
              }
            }
          }
          &__header {
            font-family: MADE Tommy Soft, Verdana, sans-serif;
            font-size: 7rem;
            color: $primaryColor;
          }
          &__budget-management-container {
            font-family: MADE Tommy Soft, Verdana, sans-serif;
            &--extra-small {
              &__header {
                font-size: 2.2rem;
                color: $colorOffWhite;
                &__icon {
                  font-size: 3rem;
                }
                &__text {
                }
              }
              &__budget-name-form,
              &__budget-savings-goal-form,
              &__budget-investment-goal-form,
              &__budget-emergency-goal-form,
              &__budget-tithing-setting-form,
              &__budget-exit-or-delete-form {
                font-family: MADE Tommy Soft, Verdana, sans-serif;
                &__budget-name-display {
                  font-family: MADE Tommy Soft, Verdana, sans-serif;
                  font-size: 3rem;
                  color: $primaryColor;
                }
                &__input {
                  font-family: MADE Tommy Soft, Verdana, sans-serif;
                  font-size: 1.6rem;
                  color: rgba($primaryColor, 0.8);
                  &:focus {
                    color: $primaryColor;
                  }
                }
                &__submit,
                &__submit--exit,
                &__submit--delete {
                  font-size: 1.6rem;
                  color: rgba($colorOffWhite, 0.8);
                  &:hover {
                    color: $colorOffWhite;
                  }
                }
              }
              &__budget-savings-goal-form,
              &__budget-investment-goal-form {
                &__input {
                  font-size: 1.6rem;
                  color: rgba($primaryColor, 0.8);
                  &:focus {
                    color: $primaryColor;
                  }
                  & + label {
                    font-size: 1.6rem;
                    color: $primaryColor;
                  }
                }
              }
              &__budget-emergency-goal-form {
                &__label--checkbox-outer {
                  font-size: 1.1rem;
                  color: $primaryColor;
                }
                &__label-container {
                  &--checkbox-container {
                    &:nth-of-type(1) {
                      font-size: 1.4rem;
                      color: $primaryColor;
                    }
                    &:nth-of-type(2) {
                      font-size: 1.4rem;
                      color: $primaryColor;
                    }
                  }
                  .checked {
                    color: $colorOffWhite;
                  }
                }
                &__selection-container {
                  &__select {
                    color: rgba($colorOffWhite, 0.8);
                    &:hover {
                      color: $colorOffWhite;
                    }
                  }
                  &__input {
                    color: rgba($primaryColor, 0.8);
                    &:hover,
                    &:focus {
                      color: $primaryColor;
                    }
                  }
                  &__input,
                  &__select {
                    font-size: 1.6rem;
                  }
                }
              }
              &__budget-tithing-setting-form {
                &__setting-container {
                  &__label-container {
                    font-size: 2rem;
                    color: rgba($primaryColor, 0.8);
                    &:hover {
                      color: $colorOffWhite;
                    }
                  }
                  .selected {
                    color: rgba($colorOffWhite, 0.8);
                  }
                }
              }
            }
          }
          &__edit-category-goals-container {
            &--large {
              font-family: MADE Tommy Soft, Verdana, sans-serif;
              &__header {
                color: $colorOffWhite;
                &__title {
                  font-size: 4.5rem;
                }
                &__icon {
                  font-size: 3rem;
                }
              }
              &__budget-goal-summary-container {
                .budget-single-goal-summary {
                  &__title {
                    font-size: 1.6rem;
                    color: $primaryColor;
                    text-align: center;
                  }
                  &__amount {
                    font-size: 3rem;
                    color: $primaryColor;
                  }
                  .positive {
                    color: $secondaryColor;
                  }
                  .negative {
                    color: $colorError;
                  }
                }
              }
              &__category-container {
                .main-category-display {
                  &__button-container {
                    font-size: 6rem;
                    .left {
                      color: rgba($primaryColor, 0.25);
                    }
                    .right {
                      color: rgba($primaryColor, 0.8);
                    }
                    .left,
                    .right {
                      &:hover {
                        color: $primaryColor;
                      }
                    }
                  }
                  &__category-display {
                    color: $primaryColor;
                    &__icon {
                      font-size: 20rem;
                    }
                    &__title {
                      font-size: 4rem;
                    }
                  }
                }
                .sub-category-display {
                  &__headers {
                    &__title-container {
                      font-size: 1.2rem;
                      color: $colorOffWhite;
                      text-align: center;
                    }
                  }
                  &__sub-category {
                    &__section {
                      &__content {
                        font-size: 1.6rem;
                        color: $primaryColor;
                      }
                      .positive {
                        color: $secondaryColor;
                      }
                      .negative {
                        color: $colorError;
                      }
                      &__set-category-timing-button {
                        font-size: 1.2rem;
                        color: rgba($primaryColor, 0.8);
                      }
                      &__input {
                        font-size: 2rem;
                        color: rgba($primaryColor, 0.8);
                        text-align: center;
                        &:focus {
                          color: $primaryColor;
                        }
                      }
                    }
                    &:hover {
                      .sub-category-display__sub-category__section {
                        &__content {
                          color: $colorOffWhite;
                        }
                        &__set-category-timing-button {
                          color: rgba($colorOffWhite, 0.8);
                          &:hover {
                            color: $colorOffWhite;
                          }
                        }
                        &__input {
                          color: rgba($colorOffWhite, 0.8);
                          &:focus {
                            color: $primaryColor;
                          }
                        }
                      }
                    }
                  }
                  &__timing-container {
                    &__close {
                      font-size: 3rem;
                      color: rgba($primaryColor, 0.8);
                      &:hover {
                        color: $primaryColor;
                      }
                    }
                    &__monthly-container,
                    &__bi-monthly-container,
                    &__bi-weekly-container,
                    &__weekly-container {
                      &__button {
                        &__icon {
                          font-family: Icomoon;
                          font-size: 4rem;
                          .path1:before {
                            color: $primaryColor;
                          }
                        }
                        &__text {
                          font-size: 2.2rem;
                          color: rgba($primaryColor, 0.8);
                        }
                        &:hover {
                          .sub-category-display__timing-container {
                            &__monthly-container,
                            &__bi-monthly-container,
                            &__bi-weekly-container,
                            &__weekly-container {
                              &__button {
                                &__icon {
                                  .path1:before {
                                    color: $tertiaryColor;
                                  }
                                  .path2:before,
                                  .path3:before {
                                    color: $colorDarkerGrey;
                                  }
                                }
                                &__text {
                                  color: $colorOffWhite;
                                }
                              }
                            }
                          }
                        }
                      }
                      &__label {
                        font-size: 1.4rem;
                        color: rgba($primaryColor, 0.8);
                        &:focus {
                          color: $primaryColor;
                        }
                        &__input,
                        &__select {
                          font-size: 1.4rem;
                          color: rgba($primaryColor, 0.8);
                          &:focus {
                            color: $primaryColor;
                          }
                        }
                      }
                      &__submit {
                        font-size: 2rem;
                        color: rgba($colorOffWhite, 0.8);
                        &:hover {
                          color: $colorOffWhite;
                        }
                      }
                    }
                  }
                }
              }
            }
          }
          &__update-budget-categories-button-container {
            font-family: MADE Tommy Soft, Verdana, sans-serif;
            &__button {
              font-size: 2rem;
              color: rgba($primaryColor, 0.8);
              &:hover {
                color: $colorOffWhite;
              }
            }
          }
        }
      }
    }
  }
}
// End of Master Grid Container

// LANDING LOGO AND SLOGAN
.logo-container {
  .logo-text {
    font-size: 7rem;
    color: $primaryColor;
  }
}
.slogan {
  font-family: MADE Tommy Soft, Verdana, sans-serif;
  font-size: 3.6rem;
  color: $primaryColor;
}

// APPLICATION NAME -- LOGGED IN
.application-name {
  font-size: 7rem;
  color: $primaryColor;
}

// APPLICATION NAVIGATION
.application-navigation {
  &__section {
    &--user-information {
      font-family: MADE Tommy Soft, Verdana, sans-serif;
      &__update-picture {
        color: rgba($colorOffWhite, 0.8);
        &--icon {
          font-size: 1.6rem;
        }
        &--text {
          font-size: 1.6rem;
        }
        &:hover {
          .app-navigation__main__user-information__update-picture--icon,
          .app-navigation__main__user-information__update-picture--text {
            color: $colorOffWhite;
          }
        }
      }
      &__username {
        font-size: 3.6rem;
        color: $colorOffWhite;
      }
    }
    &--account-links {
      font-family: MADE Tommy Soft, Verdana, sans-serif;
      &__link-container {
        &__link {
          &--link {
            font-size: 2rem;
            color: rgba($colorOffWhite, 0.8);
            &:link,
            &:visited {
              text-decoration: none;
              color: rgba($colorOffWhite, 0.8);
            }
            &:hover {
              color: $colorOffWhite;
            }
            &:active {
              color: $colorDarkGrey;
            }
          }
        }
      }
    }
    &--application-logo {
      &__text {
        font-size: 3rem;
        color: $colorOffWhite;
      }
    }
  }
  &__navigation-links {
  }
  &__logo-container {
  }
}

// BUTTON CONTAINER
.button-container {
  font-family: MADE Tommy Soft, Verdana, sans-serif;
}

// BUTTONS
.button {
  font-family: MADE Tommy Soft, Verdana, sans-serif;
  font-size: 2.2rem;
  color: rgba($primaryColor, 0.75);
  &:hover {
    color: $colorOffWhite;
  }
  &--extra-small {
    color: rgba($colorOffWhite, 0.8);
    &--icon,
    &--text {
      font-size: 1.6rem;
    }
    &:hover {
      .app-navigation__main__user-information__update-picture--icon,
      .app-navigation__main__user-information__update-picture--text {
        color: $colorOffWhite;
      }
    }
  }
  &--small {
    @extend .button;
    &-transparent {
      color: rgba($colorOffWhite, 0.8);
      &:hover {
        color: $colorOffWhite;
      }
      &__password-button {
        font-family: MADE Tommy Soft, Verdana, sans-serif;
        font-size: 1.6rem;
        color: rgba($colorOffWhite, 0.8);
        &:hover {
          color: $colorOffWhite;
        }
      }
    }
    &-create-main-category {
      @extend .button--small;
      font-size: 1.6rem;
    }
    &-create-sub-category {
      font-size: 1.6rem;
      color: rgba($primaryColor, 0.8);
      &:hover {
        color: $colorOffWhite;
      }
      &-close {
        @extend .button--small-create-sub-category;
      }
    }
  }

  &--timing-button {
    &__icon {
      font-family: Icomoon;
      font-size: 4rem;
      .path1:before {
        color: $primaryColor;
      }
    }
    &__text {
      font-size: 2.2rem;
      color: rgba($primaryColor, 0.8);
    }
    &:hover {
      .button--timing-button {
        &__icon {
          .path1:before {
            color: $tertiaryColor;
          }
          .path2:before,
          .path3:before {
            color: $colorDarkerGrey;
          }
        }
        &__text {
          color: $colorOffWhite;
        }
      }
    }
    &-submit {
      @extend .button;
      color: rgba($colorOffWhite, 0.75);
    }
  }
  &--borderless {
    @extend .button;
    font-size: 1.6rem;
    &:hover {
      color: $primaryColor;
    }
    &-narrow {
      @extend .button--borderless;
      color: rgba($colorOffWhite, 0.8);
      &:hover {
        color: $colorOffWhite;
      }
    }
  }
  &--medium-square {
    font-size: 2rem;
    color: rgba($primaryColor, 0.8);
    &__icon {
      font-size: 3rem;
    }
    &:hover {
      .button--medium-square__icon,
      .button--medium-square__text {
        color: $colorOffWhite;
      }
    }
    &__icons-container {
      .icon-container {
        font-family: MADE Tommy Soft, Verdana, sans-serif !important;
        font-size: 1.6rem;
        color: $primaryColor;
        text-align: center;
        &__icon {
          font-size: 4rem;
        }
        &__text {
        }
        &:hover {
          color: $colorOffWhite;
        }
        &--clicked {
          color: $colorOffWhite;
        }
      }
    }
  }
}

// USER PROFILE SECTION
.user-profile-section {
  .user-profile-closure-icon {
    font-size: 4rem;
    color: rgba($colorOffWhite, 0.8);
    &:hover {
      color: $colorOffWhite;
    }
  }
  &__header {
    &__text {
      font-family: MADE Tommy Soft, Verdana, sans-serif;
      font-size: 10rem;
      color: $colorOffWhite;
    }
  }
}
////////////////////////
// BUDGET CREATION

// MAIN CATEGORY
.main-category {
  font-family: MADE Tommy Soft, Verdana, sans-serif;
  font-size: 2rem;
  color: $primaryColor;
  &__icon {
    font-size: 3rem;
  }
  &__delete {
    font-size: 1.2rem;
    color: rgba($primaryColor, 0.8);
  }
  &:hover {
    color: $colorOffWhite;
    .main-category__delete {
      color: rgba($colorOffWhite, 0.8);
      &:hover {
        color: $primaryColor;
      }
    }
  }
}

// SUB-CATEGORY
.sub-category {
  &-controller {
    &__surplus-container {
      &__switch {
        &__toggle {
          &__icon {
            font-size: 1.2rem;
            color: rgba($colorOffWhite, 0.8);
          }
        }
      }
    }
  }
}

// BUDGET CREATION CONTAINER
.budget-creation-container {
  &--sub-categories {
    &__main-category-display {
      &__left-button,
      &__right-button {
        &__icon {
          font-size: 5rem;
          color: rgba($primaryColor, 0.8);
          &:hover {
            color: $primaryColor;
          }
        }
      }
      &__left-button {
        &__icon {
          color: rgba($primaryColor, 0.25);
        }
      }
      &__category-information {
        color: $primaryColor;
        &__icon {
          font-size: 16rem;
        }
        &__text {
          font-size: 4rem;
        }
      }
    }
  }
}

// MONTH CONTAINER
.month-container {
  &__header {
    font-size: 2.8rem;
    color: $colorOffWhite;
    &__title {
      font-size: 4rem;
      text-transform: uppercase;
    }
  }
  &__overall-budget-summary-container {
    &--single-summary {
      &__title {
        font-size: 1.6rem;
        color: $primaryColor;
        text-align: center;
      }
      &__amount {
        font-size: 3rem;
        color: $primaryColor;
      }
    }
  }
  &__category-container {
    .main-category-display {
      &__button-container {
        font-size: 6rem;
        .left {
          color: rgba($primaryColor, 0.25);
        }
        .right {
          color: rgba($primaryColor, 0.8);
        }
        .left,
        .right {
          &:hover {
            color: $primaryColor;
          }
        }
      }
      &__category-display {
        color: $primaryColor;
        &__icon {
          font-size: 20rem;
        }
        &__title {
          font-size: 4rem;
        }
      }
    }
    .sub-category-display {
      &__headers {
        &__title-container {
          font-size: 1.2rem;
          color: $colorOffWhite;
          text-align: center;
        }
      }
    }
  }
}

.icon-container {
  font-family: MADE Tommy Soft, Verdana, sans-serif !important;
  font-size: 1.6rem;
  color: $primaryColor;
  text-align: center;
  &__icon {
    font-size: 4rem;
  }
  &:hover {
    color: $colorOffWhite;
  }
  &--clicked {
    color: $colorOffWhite;
  }
}

// TIMING CONTAINER
.timing-container {
  &__close {
    font-size: 3rem;
    color: rgba($primaryColor, 0.8);
    &:hover {
      color: $primaryColor;
    }
  }
  &__label {
    font-size: 1.4rem;
    color: rgba($primaryColor, 0.8);
    &:focus {
      color: $primaryColor;
    }
    &__input,
    &__select {
      font-size: 1.4rem;
      color: rgba($primaryColor, 0.8);
      &:focus {
        color: $primaryColor;
      }
    }
  }
  &__submit {
    font-size: 2rem;
    color: rgba($colorOffWhite, 0.8);
    &:hover {
      color: $colorOffWhite;
    }
  }
}

// FORMS
.form {
  font-family: MADE Tommy Soft, Verdana, sans-serif;
  &-closure-icon {
    font-size: 4rem;
    color: rgba($primaryColor, 0.5);
    &:hover {
      color: $primaryColor;
    }
  }
  &__header {
    &__title {
      font-family: MADE Tommy Soft Bold, Verdana, sans-serif;
      font-size: 10rem;
      color: $primaryColor;
      &--medium {
        font-family: MADE Tommy Soft Bold, Verdana, sans-serif;
        color: $primaryColor;
        font-size: 6rem;
      }
    }
  }
  &__section {
    font-family: MADE Tommy Soft, Verdana, sans-serif;
    font-size: 2rem;
    color: rgba($primaryColor, 0.8);
    &--sub-section {
      @extend .form__section;
      color: rgba($colorOffWhite, 0.8);
    }
    &--switch {
      @extend .form__section;
    }
    &--page-number-display {
      @extend .form__section;
      font-size: 3rem;
      color: $primaryColor;
      &__number {
        @extend .form__section--page-number-display;
      }
    }
  }
  &__input {
    font-family: MADE Tommy Soft, Verdana, sans-serif;
    font-size: 2rem;
    color: rgba($primaryColor, 0.8);
    &:focus {
      color: $primaryColor;
      & > label {
        color: $primaryColor;
      }
    }

    &--small {
      @extend .form__input;
    }

    &--dark {
      @extend .form__input;
      color: rgba($colorOffWhite, 0.8);
      &:focus {
        color: $colorOffWhite;
        & + label {
          color: $colorOffWhite;
        }
      }
      &-small {
        @extend .form__input--dark;
      }
      &-extra-small {
        @extend .form__input--dark;
        font-size: 1.6rem;
      }
    }
    &--main-category-title {
      @extend .form__input;
      font-size: 1.6rem;
    }

    &--sub-category-title {
      @extend .form__input;
      font-size: 1.6rem;
    }
    &--latter-day-saint {
      @extend .form__input;
      font-size: 3rem;
      color: $colorOffWhite;
      text-transform: capitalize;
    }
    &--comms {
      @extend .form__input;
      &--email,
      &--text {
        // font-size: 2rem;
        @extend .form__input--comms;
        color: $colorOffWhite;
        &__text {
          font-size: 1.6rem;
        }
      }
    }
  }
  &__label {
    font-family: MADE Tommy Soft, Verdana, sans-serif;
    font-size: 2rem;
    color: rgba($primaryColor, 0.8);
    &--small {
      @extend .form__label;
      color: rgba($colorOffWhite, 0.8);
      &-budget {
        @extend .form__label;
        font-size: 1.6rem;
      }
    }
    &--extra-small {
      @extend .form__label--small;
      font-size: 1.6rem;
    }
    &--latter-day-saint {
      @extend .form__label;
      font-size: 2.2rem;
      &-white {
        @extend .form__label--latter-day-saint;
        color: rgba($colorOffWhite, 0.8);
      }
    }
    &--comms {
      @extend .form__label;
      color: $colorOffWhite;
    }
  }

  &__page {
    &-number {
      font-size: 2rem;
      color: $primaryColor;
    }
  }
}
.switch {
  &-container--latter-day-saint {
    .switch--latter-day-saint {
      &__text--NO,
      &__text--YES {
        font-size: 3rem;
        color: $colorOffWhite;
        text-transform: capitalize;
      }
    }
  }
  &--comms {
    &--email,
    &--text {
      font-size: 2rem;
      color: $colorOffWhite;
      &__text {
        font-size: 1.6rem;
      }
    }
  }
}

// BUDGET CARDS
.budget-card-container {
  &__card {
    font-family: MADE Tommy Soft, Verdana, sans-serif;
    &__header {
      &__text {
        font-size: 2.4rem;
        font-family: Purely Me;
        color: rgba($colorOffWhite, 0.8);
      }
      &__icon {
        font-size: 2rem;
        color: rgba($colorOffWhite, 0.8);
      }
    }
    &__content {
      &__change-photo-button {
        &__icon {
          font-size: 1.6rem;
          color: rgba($primaryColor, 0.5);
        }
      }
    }
    &__footer {
      color: rgba($colorOffWhite, 0.8);
      &--top {
        font-size: 1.8rem;
      }
      &--bottom {
        font-size: 1.8rem;
      }
    }
    &:hover {
      .budget-card-container__card__header {
        &__text {
          color: $colorOffWhite;
        }
        &__icon {
          color: $colorOffWhite;
          &:hover {
            color: $secondaryColor;
          }
        }
      }
      .budget-card-container__card__content {
        &__change-photo-button {
          &:hover {
            .budget-card-container__card__content__change-photo-button__icon {
              color: $colorOffWhite;
            }
          }
        }
      }
      .budget-card-container__card__footer {
        color: $colorOffWhite;
      }
    }
    &--create {
      &__header {
        &__text {
          font-size: 2rem;
          color: rgba($colorOffWhite, 0.8);
        }
      }
      &__icon {
        font-size: 10rem;
        color: rgba($primaryColor, 0.8);
      }
      &:hover {
        .budget-card-container__card--create__header {
          &__text {
            color: $colorOffWhite;
          }
        }
        .budget-card-container__card--create__icon {
          color: $primaryColor;
        }
      }
    }
  }
}
